/* - Header */
.header {
  padding: 50px;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 100;
  @include transition(all 0.7s cubic-bezier(0.165, 0.85, 0.45, 1));
  &.fixed {
    padding-top: 30px;
    padding-bottom: 30px;
    top: 0;
    background: $default_bg;
  }
  &:after {
    content: '';
    position: relative;
    display: block;
    clear: both;
  }
  .head-top {
    position: relative;
  }
  .menu-btn {
    position: relative;
    margin: 0 auto;
    width: 30px;
    height: 20px;
    &:before,
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 2px;
      background: $white_color;
      @include transition(all 0.3s ease 0s);
    }
    &:after {
      top: auto;
      bottom: 0;
    }
    span {
      position: absolute;
      top: 50%;
      left: 0;
      width: 100%;
      height: 2px;
      background: $white_color;
      margin-top: -1px;
      @include transition(all 0.3s ease 0s);
    }
  }
  .logo {
    position: absolute;
    overflow: hidden;
    top: 50%;
    width: 150px;
    font-size: $default_size;
    line-height: 2.5;
    font-weight: 600;
    color: rgba(255,255,255,0.5);
    white-space: nowrap;
    @include transform(translateY(-50%));
    img {
      max-width: 100%;
      width: auto;
      height: auto;
      display: block;
    }
    a {
      color: rgba(255,255,255,0.5);
      text-decoration: none;
      display: block;
    }
    strong {
      color: $white_color;
    }
  }
  .top-menu {
    text-align: right;
    .menu-top-menu-container {
      display: inline-block;
    }
    ul {
      margin: 0;
      padding: 0;
      list-style: none;
      li {
        margin-left: 40px;
        display: inline-block;
        vertical-align: top;
        position: relative;
        a {
          position: relative;
          overflow: hidden;
          display: block;
          font-size: 1em;
          line-height: 2.5;
          color: $white_color;
          font-weight: 600;
          opacity: 0.5;
          text-decoration: none;
          @include transition(color 0.7s cubic-bezier(0.165, 0.85, 0.45, 1));
        }
        &:hover > .sub-menu,
        &:hover > .children {
          opacity: 1;
          pointer-events: auto;
        }
        &:hover > a,
        &.current-menu-item > a {
          opacity: 1;
        }
        &.menu-item-has-children > a .mask-lnk:after {
          content: "\f078";
          margin-left: 5px;
          position: relative;
          top: 0;
          display: inline-block;
          font-family: 'Font Awesome 5 Free';
          font-weight: 900;
          font-size: 0.8em;
          -moz-osx-font-smoothing: grayscale;
          -webkit-font-smoothing: antialiased;
          display: inline-block;
          font-style: normal;
          font-variant: normal;
          text-rendering: auto;
          line-height: 1;
        }
      }
    }
  }
  .top-menu-nav .sub-menu,
  .top-menu-nav .children {
    position: absolute;
    left: 0;
    top: 40px;
    padding: 10px 0;
    width: 190px;
    text-align: left;
    background: $extra_bg;
    z-index: 50;
    opacity: 0;
    pointer-events: none;
    @include transition(all 0.3s ease 0s);
    @include box-shadow(0 5px 50px rgba(0,0,0,0.1));
  }
  .top-menu ul li:last-child .sub-menu,
  .top-menu-nav .children {
    right: 0;
    left: auto;
  }
  .top-menu-nav .sub-menu:before,
  .top-menu-nav .children:before {
    content: '';
    position: absolute;
    left: 10px;
    top: -12px;
    border: 6px solid transparent;
    border-bottom: 6px solid $extra_bg;
  }
  .top-menu ul li:last-child .sub-menu:before,
  .top-menu-nav .children:before {
    right: 10px;
    left: auto;
  }
  .top-menu-nav .sub-menu li,
  .top-menu-nav .children li {
    margin-left: 0;
    padding: 8px 20px;
    display: block;
    border-bottom: 1px solid $lines_color;
    height: auto;
  }
  .top-menu-nav .sub-menu li:last-child,
  .top-menu-nav .children li:last-child {
    border-bottom: none;
  }
  .top-menu-nav .sub-menu li a,
  .top-menu-nav .children li a {
    margin-top: 0;
    display: block;
    font-size: $extra_small_size;
    color: $white_color;
    opacity: 0.5;
  }
  .top-menu-nav .sub-menu li .sub-menu,
  .top-menu-nav .children li .children {
    left: 100%;
    top: -10px;
  }
  .top-menu-nav .sub-menu li .sub-menu:before,
  .top-menu-nav .children li .children:before {
    content: '';
    position: absolute;
    left: -12px;
    top: 16px;
    border: 6px solid transparent;
    border-right: 6px solid $white_color;
    border-bottom: 6px solid transparent;
  }
}

.background-enabled .header .top-menu ul li.current-menu-item > a {
  opacity: 1;
}

.hover-masks .mask-lnk,
.hover-masks-logo .mask-lnk {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  display: block;
  @include transform(translateY(0));
  -webkit-transition: opacity 0.7s cubic-bezier(0.165, 0.85, 0.45, 1), color 0.7s cubic-bezier(0.165, 0.85, 0.45, 1), transform 0.7s cubic-bezier(0.165, 0.85, 0.45, 1);
  transition: opacity 0.7s cubic-bezier(0.165, 0.85, 0.45, 1), color 0.7s cubic-bezier(0.165, 0.85, 0.45, 1), transform 0.7s cubic-bezier(0.165, 0.85, 0.45, 1);
}

.hover-masks .mask-lnk.mask-lnk-hover,
.hover-masks-logo .mask-lnk.mask-lnk-hover {
  position: absolute;
  opacity: 0;
  @include transform(translateY(32px));
}

.hover-masks a:hover .mask-lnk,
.hover-masks-logo a:hover .mask-lnk {
  opacity: 1;
  @include transform(translateY(-32px));
}

.hover-masks a:hover .mask-lnk.mask-lnk-hover,
.hover-masks-logo a:hover .mask-lnk.mask-lnk-hover {
  @include transform(translateY(0));
}

/* header active */
.header.active {
  padding-top: 4vw;
  top: 0;
  overflow: auto;
  height: 100%;
  background: $default_bg;
  -webkit-overflow-scrolling: touch;
  .menu-btn {
    &:before {
      opacity: 0;
    }
    &:after {
      bottom: 9px!important;
      @include transform(rotate(-45deg));
    }
    span {
      @include transform(rotate(45deg));
    }
  }
  .top-menu {
    height: auto;
    opacity: 1;
    visibility: visible;
    ul li .sub-menu,
    ul li .children {
      display: block;
    }
    ul > li > ul li a {
      line-height: 20px;
    }
  }
}


/* header tablet */
@media (max-width: 1199px) {
  .header .logo {
    position: relative;
    top: 0;
    @include transform(translateY(0%));
  }
  .header .menu-btn {
    display: block;
    @include transform(translateY(50%));
  }
  .header .top-menu-nav {
    display: block;
  }
  .header .top-menu {
    position: relative;
    top: 0;
    left: 0;
    margin-top: 0;
    margin-left: 0;
    margin-right: 0;
    float: none;
    width: auto;
    height: 0px;
    text-align: center;
    opacity: 0;
    visibility: hidden;
    @include transition(opacity 0.35s cubic-bezier(0.165, 0.85, 0.45, 1));
  }
  .header.active .top-menu {
    margin-top: 20px;
  }
  .header .top-menu .menu-top-menu-container {
    display: block;
  }
  .header .top-menu ul li {
    margin-left: 0;
    display: block;
  }
  .header .top-menu-nav .sub-menu, .header .top-menu-nav .children {
    position: relative;
    left: 0;
    top: 0;
    margin: 20px 0;
    width: auto;
    text-align: center;
    pointer-events: none;
    display: block;
    opacity: 0;
    visibility: hidden;
  }
  .header.active .top-menu-nav .sub-menu, .header.active .top-menu-nav .children {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
  }
  .header .top-menu-nav .sub-menu:before, .header .top-menu-nav .children:before {
    margin-left: -6px;
    left: 50%;
  }
  .header .top-menu-nav .sub-menu li .sub-menu, .header .top-menu-nav .children li .children {
    left: 0;
    top: 0;
    margin: 0 -20px;
    background: #f4f4f4;
    @include box-shadow(none);
  }
  .header .top-menu-nav .sub-menu li .sub-menu:before, .header .top-menu-nav .children li .children:before {
    left: 50%;
    top: -12px;
    border: 6px solid transparent;
    border-right: 6px solid transparent;
    border-bottom: 6px solid $white_color;
  }
  .header .top-menu ul li:last-child .sub-menu:before, .header .top-menu-nav .children:before {
    right: 50%;
    left: auto;
    margin-left: 0;
    margin-right: -6px;
  }
  .header .menu-btn {
    display: block;
    float: right;
  }
}

/* header mobile */
@media (max-width: 720px) {
  .header {
    margin: 0;
    padding: 0 30px;
    top: 30px;
  }
  .header .logo {
    padding: 0;
    max-width: 240px;
  }
  .header.active .top-menu {
    padding-bottom: 30px;
  }
  .wp-block-media-text {
    display: block;
  }
  .wp-block-media-text .wp-block-media-text__content {
    padding: 0;
  }
}

/* - Section */
.section {
  position: relative;
  padding-left: 20%;
  padding-right: 20%;
  padding-top: 70px;
  padding-bottom: 70px;
  z-index: 12;
  &:first-child {
    padding-top: 150px;
  }
  .content {
    position: relative;
    .portfolio-cols .title {
      margin-left: 0;
    }
  }
  .content-box {
    margin-right: -1px;
    padding: 35px;
    font-size: $default_size;
    background: $default_bg;
    border: 1px solid $lines_color;
    .single-post-text > *:first-child {
      margin-top: 0;
    }
  }
  .title {
    position: relative;
    margin: 0 0 35px 35px;
    .title_inner {
      display: inline-block;
      vertical-align: top;
      position: relative;
      font-size: $large_size;
      color: $white_color;
      font-weight: 600;
      @include transition(all 0.3s ease 0s);
    }
  }
}

/* section tablet */
@media (max-width: 1199px) {
  .section {
    padding-left: 50px;
    padding-right: 50px;
  }
}

/* section mobile */
@media (max-width: 720px) {
  .section {
    padding-top: 50px;
    padding-bottom: 50px;
    padding-left: 30px;
    padding-right: 30px;
  }
  .section .title {
    margin: 0 0 30px 0;
  }
}

/* - Started */
.section.started {
  margin: 0;
  padding: 0;
  position: relative;
  overflow: hidden;
  height: 100vh;
  z-index: 11;
  &.personal {
    z-index: 12;
  }
  .started-content {
    .logo {
      position: relative;
      display: inline-block;
      top: -100px;
      left: -300px;
      width: 740px;
      height: 600px;
      background-repeat: no-repeat;
      background-position: center center;
      background-size: cover;
      -webkit-clip-path: polygon(31% 0, 100% 25%, 61% 100%, 0 93%);
      clip-path: polygon(31% 0, 100% 25%, 61% 100%, 0 93%);
    }
    .h-title {
      margin: 0;
      position: absolute;
      left: calc(20% + 35px);
      bottom: 50px;
      max-width: 560px;
      font-size: 20px;
      font-weight: 300;
      color: $white_color;
      word-break: break-word;
      -ms-word-wrap: break-word;
      word-wrap: break-word;
      strong {
        color: $active_color;
      }
    }
    .h-subtitles {
      font-size: 38px;
      height: 38px;
      opacity: 0;
      &.ready {
        opacity: 1;
      }
    }
    .h-subtitle,
    .typed-subtitle,
    .typed-bread {
      margin-top: -45px;
      position: absolute;
      top: 50%;
      left: 0;
      width: 100%;
      display: block;
      font-size: 90px;
      line-height: 90px;
      font-weight: 500;
      color: $white_color;
      text-align: center;
    }
    .h-subtitle strong,
    .typed-subtitle strong {
      color: $active_color;
    }
  }
  &.layout-creative {
    margin: 0 auto;
    margin-bottom: 0;
    padding-top: 20%;
    padding-left: 20%;
    padding-right: 20%;
    padding-bottom: 70px;
    width: auto;
    height: auto !important;
    .started-content {
      padding-left: 0;
      padding-right: 0;
      display: block;
      text-align: left;
      .h-title {
        margin: 0 0 5px 33px;
        padding-right: 0;
        position: relative;
        left: 0;
        top: 0;
        bottom: auto;
        max-width: 800px;
        font-size: 44px;
        font-family: $default_font;
        font-weight: 500;
        text-transform: none;
        letter-spacing: 0em;
      }
      .h-subtitle,
      .typed-subtitle,
      .typed-bread,
      .typed-cursor {
        margin-top: 0;
        margin-left: 35px;
        position: relative;
        font-size: 18px;
        line-height: 40px;
        text-align: left;
        color: $extra_color;
      }
      .h-subtitle,
      .typed-subtitle {
        top: 0;
      }
      .h-subtitle p {
        font-size: 18px;
        line-height: 40px;
      }
    }
  }
  &.personal {
    text-align: center;
    .started-content {
      .h-title {
        margin-left: -85px;
        margin-top: 85px;
        position: absolute;
        left: 50%;
        top: 50%;
        bottom: auto;
        width: 560px;
        text-align: left;
      }
      .typed-subtitle,
      .typed-bread {
        margin-top: 0;
        top: auto;
        bottom: 50px;
        left: 20%;
        line-height: 74px;
        text-align: left;
      }
    }
  }
  &.section-title {
    .started-content {
      .h-title {
        margin-top: 0;
        padding-right: 0;
        position: relative;
        top: 0;
        left: 0;
        bottom: 0;
        max-width: 100%;
        font-size: 7vw;
        line-height: 7vw;
        font-weight: 600;
        text-align: center;
      }
      .h-subtitles {
        height: auto;
      }
      .h-subtitle,
      .typed-subtitle,
      .typed-bread {
        margin: 0;
        padding-left: 35px;
        position: absolute;
        left: 20%;
        top: auto;
        bottom: 50px;
        width: auto;
        max-width: 560px;
        font-size: 20px;
        line-height: $line_height;
        font-weight: 400;
        text-align: left;
      }
      .h-subtitle p {
        margin: 0;
        font-size: 20px;
        line-height: $line_height;
      }
    }
  }
  .mouse_btn {
    font-size: $large_size;
    color: $active_color;
    text-align: center;
    position: absolute;
    width: 20px;
    height: 40px;
    left: 50%;
    bottom: 7%;
    margin-left: -10px;
    z-index: 2;
    .icon {
      position: relative;
      top: 0px;
      animation: mouse-anim 1s ease-out 0s infinite;
    }
  }
  .swiper-nav {
    position: absolute;
    left: calc(20% + 35px);
    bottom: 120px;
    width: 66px;
    height: 24px;
    z-index: 3;
    .swiper-button-prev,
    .swiper-button-next {
      width: 20px;
      height: 20px;
      margin-top: -10px;
      background: none;
      font-size: 22px;
      color: $white_color;
      text-decoration: none;
      @include transition(all 0.3s ease 0s);
    }
  }
}

.section.started + .section,
.section.started.layout-creative + .section {
  margin-top: 70px;
}

.section.started.section-title .started-content .typed-bread,
.section.started.layout-creative .started-content .typed-bread,
.background-enabled .section.started.section-title .started-content .typed-bread,
.background-enabled .section.started.layout-creative .started-content .typed-bread {
  top: auto;
  font-size: $default_size;
  line-height: 1.9;
  color: rgba(255,255,255,0.5);
  font-weight: 400;
  opacity: 1;
}

.section.started.section-title .started-content .typed-bread a,
.section.started.layout-creative .started-content .typed-bread a {
  color: $white_color;
  text-decoration: none;
  font-weight: 500;
  opacity: 1;
}

.section.started .started-content .typed-cursor,
.section.started.layout-creative .mouse_btn,
.section.started.layout-post-creative .mouse_btn {
  display: none!important;
}

.section.started .swiper-button-prev, .section.started .swiper-container-rtl .swiper-button-next {
  left: 0;
  right: auto;
}

.section.started .swiper-button-next, .section.started .swiper-container-rtl .swiper-button-prev {
  left: auto;
  right: 0;
}

.started-carousel {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  .swiper-container,
  .swiper-wrapper {
    height: 100%;
  }
  .swiper-slide {
    opacity: 1 !important;
    &.first {
      z-index: 2;
    }
  }
  .main-img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    clip-path: polygon(0 0, 20% 0, 20% 0, 40% 0, 40% 0, 60% 0, 60% 0, 80% 0, 80% 0, 100% 0, 100% 0, 100% 100%, 100% 100%, 80% 100%, 80% 100%, 60% 100%, 60% 100%, 40% 100%, 40% 100%, 20% 100%, 20% 100%, 0 100%);
    -webkit-clip-path: polygon(0 0, 20% 0, 20% 0, 40% 0, 40% 0, 60% 0, 60% 0, 80% 0, 80% 0, 100% 0, 100% 0, 100% 100%, 100% 100%, 80% 100%, 80% 100%, 60% 100%, 60% 100%, 40% 100%, 40% 100%, 20% 100%, 20% 100%, 0 100%);
    @include transition(1s all cubic-bezier(0.165, 0.85, 0.45, 1) 0s);
  }
  .swiper-slide.swiper-clip-active .main-img {
    clip-path: polygon(0 0, 20% 0, 20% 100%, 40% 100%, 40% 0, 60% 0, 60% 100%, 80% 100%, 80% 0, 100% 0, 100% 0, 100% 100%, 100% 0%, 80% 0%, 80% 100%, 60% 100%, 60% 0%, 40% 0%, 40% 100%, 20% 100%, 20% 0%, 0 0%);
    -webkit-clip-path: polygon(0 0, 20% 0, 20% 100%, 40% 100%, 40% 0, 60% 0, 60% 100%, 80% 100%, 80% 0, 100% 0, 100% 0, 100% 100%, 100% 0%, 80% 0%, 80% 100%, 60% 100%, 60% 0%, 40% 0%, 40% 100%, 20% 100%, 20% 0%, 0 0%);
  }
}

.section.started .mouse_btn {
  left: auto;
  right: calc(20% + 35px);
  margin-left: 0;
}

/* section started desktop */
@media (min-width: 1200px) {
  .section.started .started-content .logo {
    top: -11.5vh;
    left: -18.5vw;
    width: 56vw;
    height: 80vh;
  }
  .section.started.personal .started-content .h-title {
    margin-left: 0;
    margin-top: 0;
    left: 50vw;
    top: 60vh;
    bottom: auto;
  }
}

/* section started tablet */
@media (max-width: 1199px) {
  .section.started.layout-creative, .section.started.layout-post-creative {
    padding-top: 35vw;
    padding-left: 50px;
    padding-right: 50px;
  }
  .section.started .started-content .h-title {
    left: 50px;
  }
  .section.started .started-content .h-title br {
    display: none;
  }
  .section.started .started-content .h-subtitle,
  .section.started .started-content .typed-subtitle,
  .section.started .started-content .typed-bread {
    margin-top: -35px;
    font-size: 60px;
    line-height: 70px;
  }
  .section.started.section-title .started-content .h-subtitle,
  .section.started.section-title .started-content .typed-subtitle,
  .section.started.section-title .started-content .typed-bread {
    padding-left: 0;
    left: 50px;
  }
  .section.started.personal .started-content .typed-subtitle,
  .section.started.personal .started-content .typed-bread {
    left: 50px;
    width: auto;
  }
  .section.started .swiper-nav {
    left: 50px;
  }
  .section.started .started-content .logo {
    top: -50px;
    left: -150px;
    width: 640px;
    height: 560px;
  }
  .section.started.personal .started-content .h-title {
    margin-left: 15px;
    margin-top: 115px;
    width: 320px;
  }
  .section.started.layout-creative .started-content .h-title,
  .section.started.layout-creative .started-content .h-subtitle,
  .section.started.layout-creative .started-content .typed-subtitle,
  .section.started.layout-creative .started-content .typed-bread,
  .section.started.layout-creative .started-content .typed-cursor {
    margin-left: 0;
  }
  .section.started .mouse_btn {
    left: auto;
    right: 120px;
    margin-left: 0;
  }
}

/* section started mobile */
@media (max-width: 720px) {
  .section.started {
    padding: 0 30px;
  }
  .section.started.layout-creative,
  .section.started.layout-post-creative {
    padding-top: 50vw;
    padding-left: 30px;
    padding-right: 30px;
    padding-bottom: 30px;
  }
  .section.started .started-content .h-title {
    left: 0;
    bottom: 30px;
    padding-right: 50px;
    font-size: 17px;
  }
  .section.started .started-content .h-subtitle,
