/* - About */
.section.about {
  .image {
    float: left;
    width: 160px;
    font-size: 0;
    img {
      width: 100%;
      height: auto;
    }
  }
  .desc {
    margin-left: 195px;
    p {
      word-break: break-word;
      &:first-child {
        margin-top: 0;
      }
    }
  }
}

.info-list {
  margin-bottom: 20px;
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
    li {
      display: inline-block;
      vertical-align: top;
      width: 48%;
      margin: 0 0 6px 0;
      strong {
        font-weight: 400;
        color: $active_color;
      }
    }
  }
}

/* section about fullhd */
@media (min-width: 1700px) {
  .info-list ul li {
    width: 32%;
  }
}

/* section about tablet */
@media (max-width: 1199px) {
  .section.about .image {
    width: 120px;
    margin-right: 0;
    margin-bottom: 25px;
  }
  .section.about .desc {
    margin-left: 145px;
  }
}

/* section about mobile */
@media (max-width: 720px) {
  .section.about .image {
    margin: 0 auto 20px auto;
    float: none;
    width: 100px;
    text-align: center;
  }
  .section.about .desc {
    margin-left: 0;
    text-align: center;
  }
  .info-list ul li {
    width: 100%;
  }
}

/* - Resume */
.section {
  .col {
    &:nth-child(odd) {
      padding-right: 35px;
    }
    &:nth-child(even) {
      padding-left: 35px;
      .content-box {
        margin-right: -1px;
      }
    }
  }
}

.resume-items {
  .resume-item {
    position: relative;
    margin: 70px 0 0 0;
    &:first-child {
      margin: 0;
    }
    .date {
      position: relative;
      margin: 0 0 5px 0;
      display: inline-block;
      font-size: $extra_small_size;
      color: $extra_color;
    }
    .name {
      margin: 0 0 15px 0;
      color: $light_color;
      font-weight: 500;
    }
    &.active {
      .date {
        color: $active_color;
        font-weight: 600;
      }
    }
  }
}

/* section resume tablet */
@media (max-width: 1199px) {
  .section .col:nth-child(odd) {
    padding-right: 20px;
  }
  .section .col:nth-child(even) {
    padding-left: 20px;
  }
  .resume-items .resume-item {
    margin: 40px 0 0 0;
  }
}

/* section resume mobile */
@media (max-width: 720px) {
  .section .col:nth-child(odd) {
    padding-right: 0;
    margin-bottom: 40px;
  }
  .section .col:nth-child(even) {
    padding-left: 0;
  }
}

/* - Skills */
.skills {
  ul {
    margin: -35px 0 0 0;
    padding: 0;
    list-style: none;
    font-size: 0;
    li {
      position: relative;
      padding: 35px 0 0 0;
      &:after {
        display: none;
      }
      &:last-child {
        padding-bottom: 0;
      }
      .name {
        margin: 0 0 10px 0;
        position: relative;
        font-size: $default_size;
        color: $light_color;
        font-weight: 500;
      }
      .progress {
        display: block;
        height: 4px;
        position: relative;
        width: 100%;
        background: rgba(255,255,255,0.1);
        .percentage {
          left: 0;
          top: 0;
          position: absolute;
          height: 4px;
          width: 0%;
          background: $active_color;
          @include transition(all 0.3s ease 0s);
          .percent {
            position: absolute;
            top: -34px;
            right: 0;
            font-size: $default_size;
            font-weight: 400;
          }
        }
      }
    }
  }
  &.list {
    ul {
      margin: 0 -35px;
      li {
        padding-top: 0;
        padding-bottom: 0;
        padding-left: 35px;
        padding-right: 35px;
        display: inline-block;
        vertical-align: top;
        width: 50%;
        .name {
          padding-left: 35px;
          position: relative;
          &:before {
            content: "\f00c";
            position: absolute;
            left: 0;
            top: 3px;
            display: inline-block;
            color: $active_color;
            font-family: 'Font Awesome 5 Free';
            font-weight: 900;
            -moz-osx-font-smoothing: grayscale;
            -webkit-font-smoothing: antialiased;
            display: inline-block;
            font-style: normal;
            font-variant: normal;
            text-rendering: auto;
            line-height: 1;
          }
        }
        .progress {
          display: none;
        }
      }
    }
  }
  &.dotted {
    ul {
      margin-left: -35px;
      margin-right: -35px;
      li {
        padding-left: 35px;
        padding-right: 35px;
        width: 50%;
        display: inline-block;
        vertical-align: top;
        .progress {
          height: 14px;
          background: transparent!important;
          .percentage {
            position: relative;
            overflow: hidden;
            top: 0;
            height: 14px;
            background: transparent!important;
            z-index: 2;
          }
          .dg,
          .da {
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            display: flex;
            justify-content: space-between;
            height: 14px;
            span {
              display: block;
              width: 14px;
              height: 14px;
              background: rgba(255,255,255,0.1);
              @include border-radius(14px);
            }
          }
          .da {
            top: 0;
            span {
              background: $active_color;
            }
          }
        }
      }
    }
  }
  &.circles {
    ul {
      li {
        margin-top: 35px;
        padding: 0 0 60px 0;
        display: inline-block;
        vertical-align: top;
        width: 25%;
        &:last-child {
          padding-bottom: 40px;
        }
        .name {
          margin: 0 0 0 -45px;
          position: absolute;
          top: 105px;
          left: 50%;
          width: 90px;
          text-align: center;
        }
        .progress {
          margin: 0 auto;
          position: relative;
          font-size: 90px;
          width: 90px;
          height: 90px;
          background: #282828;
          @include border-radius(90px);
          &:after {
            content: '';
            position: absolute;
            top: 0.09em;
            left: 0.09em;
            width: 0.82em;
            height: 0.82em;
            display: block;
            background-color: $default_bg;
            @include border-radius(50%);
          }
          span {
            position: absolute;
            width: 100%;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            font-size: $default_size;
            line-height: 90px;
            text-align: center;
            white-space: nowrap;
            z-index: 2;
          }
          .percentage {
            display: none;
          }
          .slice {
            position: absolute;
            width: 1em;
            height: 1em;
            clip: rect(0em, 1em, 1em, 0.5em);
          }
        }
      }
    }
  }
}

