/* section skills fullhd */
@media (min-width: 1700px) {
  .skills.list ul li {
    width: 33.333%;
  }
}

/* section skills mobile */
@media (max-width: 720px) {
  .skills ul {
    margin: -20px 0 0 0;
  }
  .skills ul li {
    padding: 20px 0 0 0;
  }
  .skills.dotted ul li {
    width: 100%;
  }
  .skills.circles ul li {
    width: 50%;
  }
  .skills.list ul li {
    width: 100%;
  }
  .skills.list ul li:last-child,
  .skills.list ul li:last-child .name {
    margin-bottom: 0;
  }
}

/* - Clients */

.clients-items {
  margin: -35px -35px 0 -35px;
  font-size: 0;
  .clients-col {
    padding: 35px 35px 0 35px;
    display: inline-block;
    vertical-align: top;
    width: 25%;
  }
}

.clients-item {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 100%;
  height: 100px;
  opacity: 0.4;
  @include transition(opacity 1s cubic-bezier(0.165, 0.85, 0.45, 1) 0s);
  a {
    text-decoration: none;
  }
  img {
    max-width: 100%;
    max-height: 100px;
  }
  &:hover {
    opacity: 1;
  }
}

/* section clients mobile */
@media (max-width: 720px) {
  .clients-items {
    margin: -20px -20px 0 -20px;
    .clients-col {
      padding: 20px 20px 0 20px;
      width: 50%;
    }
  }
}

/* - Service */
.service-items {
  margin-top: -70px;
  font-size: 0;
  text-align: center;
  .service-col {
    margin-top: 70px;
    display: inline-block;
    vertical-align: top;
    width: 50%;
    &:nth-child(odd) .content-box {
      margin-right: 35px;
    }
    &:nth-child(even) .content-box {
      margin-left: 35px;
    }
  }
  .service-item {
    .icon {
      font-size: $default_icon_size;
      line-height: normal;
      color: $active_color;
    }
    .name {
      margin: 15px 0;
      color: $light_color;
      font-weight: 500;
    }
  }
}

/* section service tablet */
@media (max-width: 1199px) {
  .service-items {
    margin-top: -40px;
  }
  .service-items .service-col {
    margin-top: 40px;
  }
  .service-items .service-col:nth-child(odd) .content-box {
    margin-right: 20px;
  }
  .service-items .service-col:nth-child(even) .content-box {
    margin-left: 20px;
  }
}

/* section service mobile */
@media (max-width: 720px) {
  .service-items .service-col {
    width: 100%;
  }
  .service-items .service-col:nth-child(odd) .content-box {
    margin-right: 0;
  }
  .service-items .service-col:nth-child(even) .content-box {
    margin-left: 0;
  }
}

/* - Portfolio */
.section.works {
  padding-bottom: 0;
  .filter-menu {
    margin-bottom: 70px;
  }
  .filters {
    margin-top: -10px;
    input {
      display: none;
    }
    .btn-group {
      display: inline-block;
      vertical-align: top;
    }
    label {
      display: inline-block;
      vertical-align: top;
      margin-right: 25px;
      margin-top: 10px;
      font-size: $default_size;
      color: $light_color;
      font-weight: 400;
      cursor: pointer;
      position: relative;
      padding-bottom: 0;
      @include transition(color 0.7s cubic-bezier(0.165, 0.85, 0.45, 1));
      &.glitch-effect {
        color: $active_color;
      }
    }
  }
}

.box-items {
  position: relative;
  margin-left: -50px;
  margin-right: -50px;
  font-size: 0px;
  .box-item {
    width: 50%;
    margin: 0;
    padding: 0 50px 100px 50px;
    position: relative;
    text-align: center;
    font-size: 13px;
    float: left;
    .image {
      position: relative;
      overflow: hidden;
      a {
        display: block;
        font-size: 0;
        img {
          width: 100%;
          height: auto;
          position: relative;
          top: 0;
        }
      }
      .info {
        text-align: center;
        width: 100%;
        height: 100%;
        display: block;
        position: absolute;
        left: 0;
        top: 0;
        .icon {
          position: absolute;
          top: 20px;
          right: 20px;
          color: $default_bg;
          font-size: 18px;
          display: inline-block;
          font-weight: normal;
          opacity: 0;
          @include transform(translateY(-15px));
          @include transition(all 0.7s cubic-bezier(0.165, 0.85, 0.45, 1) 0s);
        }
      }
      .desc {
        padding: 10px 20px;
        position: relative;
        display: block;
        opacity: 0;
        @include transform(translateY(-15px));
        @include transition(all 0.7s cubic-bezier(0.165, 0.85, 0.45, 1) 0s);
        .name {
          display: block;
          font-size: $large_size;
          font-weight: 600;
          color: $default_bg;
          word-wrap: break-word;
        }
      }
      &:hover {
        .icon {
          opacity: 0.6;
          @include transform(translateY(0px));
          @include transition(all 0.7s cubic-bezier(0.165, 0.85, 0.45, 1) 0.35s);
        }
        .desc {
          top: 0;
          opacity: 1;
          @include transform(translateY(0px));
          @include transition(all 0.7s cubic-bezier(0.165, 0.85, 0.45, 1) 0.35s);
        }
      }
    }
    .category {
      margin: 0;
      display: block;
      font-size: $small_size;
      color: $default_bg;
      opacity: 0.6;
    }
  }
}

.portfolio-cols {
  display: flex;
}

.description-col {
  width: 78%;
}

.details-col {
  padding-left: 50px;
  width: 24%;
}

.single-post-text {
  .details-list {
    li {
      margin-bottom: 20px;
      padding-left: 0;
      font-size: 13px;
      text-transform: uppercase;
      &:before {
        display: none;
      }
      strong {
        margin-bottom: 5px;
        display: block;
        color: $light_color;
        font-weight: 500;
      }
    }
  }
}

/* section portfolio tablet */
@media (max-width: 1199px) {
  .section.works .filter-menu {
    margin-bottom: 40px;
  }
  .section .content-box {
    padding: 25px;
  }
  .box-items {
    margin-left: -20px;
    margin-right: -20px;
  }
  .box-items .box-item {
    width: 50%;
    padding: 0 20px 40px 20px;
  }
  .portfolio-cols {
    display: block;
  }
  .description-col {
    width: 100%;
  }
  .details-col {
    width: 100%;
  }
  .details-col {
    padding: 40px 0 0 0;
    width: 100%;
  }
  .section.works .filters label {
    margin-right: 15px;
  }
}

/* section portfolio mobile */
@media (max-width: 720px) {
  .section.works .filters label {
    font-size: $small_size;
  }
  .box-items {
    margin-left: 0;
    margin-right: 0;
  }
  .box-items .box-item {
    padding-left: 0;
    padding-right: 0;
    float: none;
    width: 100%;
  }
  .box-items .box-item:last-child {
    padding-bottom: 0;
  }
}

/* - Contacts */
.section.contacts {
  .alert-success {
    display: none;
    p {
      margin: 0;
      font-size: $default_size;
      color: $light_color;
    }
  }
}

.map {
  position: absolute;
  left: 0;
  top: -5vh;
  width: 100vw;
  height: 110vh;
}

/* - Pricing */
.pricing-items {
  margin: 0 -35px;
  font-size: 0;
  .pricing-col {
    padding: 0 35px;
    display: inline-block;
    vertical-align: top;
    width: 50%;
  }
}

.pricing-item {
  position: relative;
  text-align: center;
  .icon {
    font-size: 36px;
    line-height: normal;
    color: $active_color;
  }
  .name {
    margin: 15px 0;
    color: $light_color;
    font-weight: 500;
  }
  .amount {
    margin: 30px 0;
    .number {
      margin: 0;
      position: relative;
      display: inline-block;
      vertical-align: middle;
      font-size: $large_size * 2;
      line-height: $large_size;
      font-weight: 600;
      color: $light_color;
      .dollar,
      .period {
        position: absolute;
        left: -15px;
        top: -5px;
        display: inline-block;
        vertical-align: top;
        font-size: $small_size;
        line-height: $small_size;
        color: $extra_color;
        font-weight: 400;
      }
      .period {
        left: auto;
        right: -55px;
        top: auto;
        bottom: -5px;
        width: 50px;
        text-align: left;
        white-space: nowrap;
      }
    }
  }
  .feature-list {
    margin-bottom: 30px;
    ul {
      margin: 0;
      padding: 0;
      list-style: none;
      li {
        margin: 10px 0;
        &:last-child {
          margin-bottom: 0;
          border-bottom: none;
        }
        &.disable {
          text-decoration: line-through;
          opacity: 0.5;
        }
        strong {
          margin: 0 0 0 5px;
          padding: 0 5px;
          display: inline-block;
          vertical-align: middle;
          font-size: $extra_small_size;
          color: $default_bg;
          background: $active_color;
          font-weight: 300;
          @include border-radius(2px);
        }
      }
    }
  }
}

/* section pricing tablet */
@media (max-width: 1199px) {

}

/* section pricing mobile */
@media (max-width: 720px) {
  .pricing-items {
    .pricing-col {
      margin-bottom: 40px;
      width: 100%;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

/* - Team */
.team-items {
  margin: 0 -35px;
  font-size: 0;
  .team-col {
    padding: 0 35px;
    display: inline-block;
    vertical-align: top;
    width: 50%;
  }
}

.team-item {
  position: relative;
  text-align: center;
  .image {
    font-size: 0;
    img {
      max-width: 100%;
      height: auto;
    }
  }
  .name {
    margin: 30px 0 5px 0;
    color: $light_color;
    font-weight: 500;
  }
  .category {
    margin: 0 0 5px 0;
    position: relative;
    display: inline-block;
    color: $extra_color;
  }
  .soc {
    a {
      margin: 0 5px;
      display: inline-block;
      vertical-align: top;
      text-align: center;
      color: $default_color;
      @include transition(all 0.7s cubic-bezier(0.165, 0.85, 0.45, 1));
      .icon {
        font-size: $small_size;
        color: $default_color;
      }
      &:hover {
        color: $white_color;
        opacity: 1;
        .icon {
          color: $white_color;
        }
      }
    }
  }
}

/* section team tablet */
@media (max-width: 1199px) {

}

/* section team mobile */
@media (max-width: 720px) {
  .team-items .team-col {
    margin-bottom: 40px;
    width: 100%;
    &:last-child {
      margin-bottom: 0;
    }
  }
}

/* - Testimonials */
.reviews-carousel {
  position: relative;
  width: calc(100% + 1px);
  .content-box {
    margin-right: 0;
  }
  .swiper-nav {
    position: absolute;
    left: auto;
    right: 35px;
    top: -58px;
    height: 24px;
    z-index: 3;
    .swiper-button-prev,
    .swiper-button-next {
      margin-left: 15px;
      margin-top: 0;
      position: relative;
      top: 0;
      left: 0;
      right: auto;
      width: 20px;
      height: 20px;
      background: none;
      font-size: 22px;
      color: #FFF;
      text-decoration: none;
      @include transition(all 0.3s ease 0s);
    }
  }
}

.reviews-item {
  position: relative;
  .image {
    float: left;
    margin-bottom: 15px;
    margin-right: 15px;
    width: 70px;
    font-size: 0;
    img {
      width: 100%;
      height: auto;
      @include border-radius(100%);
    }
  }
  .info {
    margin-top: 10px;
    margin-left: 85px;
  }
  .name {
    margin: 0 0 5px 0;
    color: $light_color;
    font-weight: 500;
  }
  .company {
    font-size: $small_size;
    color: $extra_color;
  }
  .text {
    clear: both;
  }
}

/* section testimonials tablet */
@media (max-width: 1199px) {

}

/* section testimonials mobile */
@media (max-width: 720px) {
  .reviews-carousel .swiper-nav {
    right: 0;
  }
}

/* - Blog */
.started-content .date {
  margin: 0 0 10px;
  display: inline-block;
  padding: 0 5px;
  height: 20px;
  line-height: 18px;
  font-weight: 500;
  font-size: 11px;
  color: $active_color;
  border: 1px solid $active_color;
}

.single-post-text, .comment-text {
  word-wrap: break-word;
}

.single-post-text:after {
  content: '';
  position: relative;
  clear: both;
  display: block;
}

.single-post-text .content-box *, .comment-text .content-box * {
  margin-top: 0;
}

.single-post-text img, .comment-text img {
  max-width: 100%;
  height: auto;
}

.single-post-text p:last-child {
  margin-bottom: 0;
}

.single-post-text p a, .comment-text p a {
  color: $active_color;
}

.single-post-text p a:hover, .comment-text p a:hover {
  text-decoration: underline;
}

blockquote {
  margin: 25px 0;
  padding: 0 0 0 20px;
  font-size: $small_size;
  line-height: 24px;
  color: #999;
  font-style: italic;
  font-weight: 300;
  border-left: 2px solid $active_color;
}

blockquote code {
  padding: 2px;
  display: inline;
}

iframe, object {
  max-width: 100%;
}

.single-post-text ul, .comment-text ul {
  list-style: none;
}

.single-post-text ol, .comment-text ol {
  list-style-position: inside;
}

.single-post-text ul > li, .single-post-text ol > li, .comment-text ul > li, .comment-text ol > li {
  margin: 5px 0;
  padding: 0 0 0 18px;
  position: relative;
  font-weight: 400;
}

.single-post-text ol > li, .comment-text ol > li {
  margin: 5px 0;
  padding: 0;
  position: relative;
  font-size: $small_size;
  font-weight: 400;
}

.single-post-text ul > li:before, .comment-text ul > li:before {
  margin: 0;
  content: '';
  position: absolute;
  left: 0;
  top: 10px;
  width: 4px;
  height: 4px;
  background: $active_color;
  @include border-radius(4px);
}

.wp-block-gallery .blocks-gallery-image, .wp-block-gallery .blocks-gallery-item {
  padding: 0;
}

.wp-block-gallery .blocks-gallery-image:before, .wp-block-gallery .blocks-gallery-item:before {
  display: none;
}

.single-post-text figure, .comment-text figure {
  margin: 0 0 30px 0;
  max-width: 100%;
}

.single-post-text figure .wp-caption-text, .comment-text figure .wp-caption-text, .gallery-caption, .bypostauthor {
  margin-top: 10px;
  font-size: $extra_small_size;
  font-style: italic;
  color: #999;
}

.gallery {
  margin: 30px -10px;
}

.single-post-text .gallery {
  margin-bottom: 0;
}

.gallery .gallery-item {
  margin: 0;
  padding: 10px;
  display: inline-block;
  vertical-align: top;
