/* - Footer */
.footer {
  padding: 0;
  position: fixed;
  bottom: 50px;
  left: 50px;
  right: 50px;
  width: auto;
  height: 1px;
  z-index: 100;
  .copy {
    position: absolute;
    left: 0;
    bottom: 0;
  }
  p,
  .copy {
    margin: 2px 0 0 0;
    font-size: $small_size;
    line-height: 2;
    color: $default_color;
  }
  .soc-box {
    position: absolute;
    right: 0;
    bottom: 0;
    float: right;
    width: 24px;
  }
  .soc a {
    display: block;
    margin-bottom: 20px;
    text-align: center;
    color: $default_color;
    @include transition(all 0.7s cubic-bezier(0.165, 0.85, 0.45, 1));
    &:last-child {
      margin-bottom: 0;
    }
    .icon {
      font-size: 18px;
      color: $default_color;
    }
    &:hover {
      color: $white_color;
      opacity: 1;
      .icon {
        color: $white_color;
      }
    }
  }
}

.follow-label {
  padding-right: 75px;
  position: absolute;
  bottom: 100%;
  right: 0;
  color: $default_color;
  font-size: $small_size;
  line-height: 24px;
  height: 24px;
  white-space: nowrap;
  transform-origin: 100% 0;
  text-align: right;
  @include transform(rotate(90deg));
  &:after {
    content: '';
    position: absolute;
    top: 50%;
    right: 0;
    width: 50px;
    height: 1px;
    background: $white_color;
  }
}

/* section footer tablet */
@media (max-width: 1199px) {
  .footer .copy {
    display: none;
  }
  .footer .follow-label {
    display: none;
  }
  .footer {
    padding: 50px 0;
    position: relative;
    top: 0;
    bottom: auto;
    left: 0;
    right: auto;
    height: auto;
  }
  .footer .copy {
    margin-bottom: 10px;
    display: block;
    position: relative;
    text-align: center;
  }
  .footer .soc-box {
    position: relative;
    right: auto;
    left: 0;
    top: 0;
    bottom: auto;
    float: none;
    width: auto;
    text-align: center;
  }
  .footer .soc a {
    margin: 0 10px;
    display: inline-block;
  }
  .home .footer {
    padding: 0;
    position: fixed;
    bottom: 50px;
    top: auto;
    left: 50px;
    right: 50px;
    height: 1px;
  }
  .home .footer .copy {
    display: none;
  }
  .home .footer .soc-box {
    position: absolute;
    right: 0;
    left: auto;
    top: auto;
    bottom: 0;
    float: right;
    width: 20px;
  }
  .home .footer .soc a {
    margin: 10px 0 0 0;
    display: block;
  }
  .home .footer .soc a .icon {
    font-size: 17px;
  }
}

/* section footer mobile */
@media (max-width: 720px) {
  .home .footer {
    bottom: 30px;
    top: auto;
    left: 30px;
    right: 30px;
  }
}

